.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#uploadDiv {
  width: 300px;
  height: 200px;
  padding: 15px;
  background-color: black;
  box-shadow: 10px 10px 5px grey;
  align-content: center;
  border-radius: 5%;
}
.center {
  margin: auto;
  width: 50%;
  padding: 10px;
  margin-top: 10%;
}
.progressBar {
  width: "25%";
  aria-valuenow: "0";
  aria-valuemin: "0";
  aria-valuemax: "100";
}
.hidden {
  visibility: hidden;
}
.file {
  background-color: white;
  box-shadow: 1px 1px 5px grey;
  border-radius: 5%;
  width: 100%;
  color: rgb(6, 173, 0);
}
#a {
  position: relative;
  left: 70px;
  top: 10px;
}
.animatedDiv {
  width: 100px;
  height: 100px;
  background: red;
  position: relative;
  -webkit-animation: mymove 0.5s;
          animation: mymove 0.5s;
}
@-webkit-keyframes mymove {
  from {
    left: -100px;
  }
  to {
    left: 0px;
  }
}
@keyframes mymove {
  from {
    left: -100px;
  }
  to {
    left: 0px;
  }
}

